/* ----------- iPhone X ----------- */

/* Portrait and Landscape */
@media only screen and (min-device-width: 375px) and (max-device-width: 812px) and (-webkit-min-device-pixel-ratio: 3) {
    .wrapper {
        width: 100vw;
        height: 100vh;
        background-color: $cc01;

        .bk_left {
            clear: both;
            position: relative!important;
            width: 100vw;
            height: 50vh;
            background-color: $cc07;
        }

        .bk_right {
            clear: both;
            position: relative;
            width: 100vw;
            height: 50vh;
        }
    }

    .lyts {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr;
        grid-auto-columns: 1fr;
        grid-auto-rows: 1fr;
        gap: 0px 0px;
        grid-auto-flow: row;
        grid-template-areas:
            "grd_top_nav"
            "grd_cont"
            "grd_bottom_nav";
        //background-color: $cc01;
        width: 100vw;
        height: 100vh;
        position: absolute;
        top: 0;
        left: 0;

        .grd_top_nav {

            .grd_left_top {
                padding: 8% 0 8% 8% !important;
                grid-area: grd_left_top;

                svg {
                    height: 10vw !important;
                    width: 10vw !important;

                    .pathcc {
                        fill: $cc08;
                    }
                }
            }

            .grd_right_top {
                padding: 8% 8% 0 0 !important;
                grid-area: grd_right_top;

                div {
                    float: right;
                }
            }

        }

        .grd_cont {
            grid-area: grd_cont;

            //LYT INT1
            .int1 {

                .grd_line1 {
                    grid-area: grd_line1;

                    h1 {
                        font-size: 6em;
                        margin: 10vh 0 !important;
                        text-align: center;
                    }
                }

                .grd_line2 {
                    grid-area: grd_line2;

                    .video_player {
                        height: auto;
                    }
                }
            }

            //[END] LYT INT1

            //LYT INT2
            .int2 {
                display: grid;
                grid-template-columns: 1fr;
                grid-template-rows: auto auto;
                grid-auto-columns: 1fr;
                grid-auto-rows: 1fr;
                gap: 0px 0px;
                grid-auto-flow: row;
                grid-template-areas:
                    "grd_line1"
                    "grd_line2"
                    "grd_line3";
                height: 100%;
                //display: none;

                .grd_line1 {
                    display: flex;
                    justify-self: center;
                    align-items: center;
                    grid-area: grd_line1;

                    h1 {
                        padding: 0 1em;
                        text-align: center;
                    }
                }

                .grd_line2 {
                    display: flex;
                    grid-area: grd_line2;

                    div {
                        margin: 0 auto;

                        div {
                            float: left;

                            div {
                                clear: both;
                                width: 100%;
                                height: auto;

                                svg {
                                    margin: 50px 32px 0;
                                    cursor: pointer;
                                    box-shadow: 0.2rem 0.8rem 1.6rem var(--colorTrans2);
                                    height: 23vw !important;
                                    width: 23vw !important;
                                    border-radius: 50%;
                                    -moz-border-radius: 50%;
                                    -webkit-border-radius: 50%;

                                    @media screen and (min-width: $xlarge) {
                                        height: 30vw !important;
                                        width: 30vw !important;
                                    }

                                    @media screen and (min-width: $xxlarge) {
                                        height: 15vw !important;
                                        width: 15vw !important;
                                    }

                                }

                                @media screen and (max-width: 447px) {
                                    clear: both;
                                    float: none;
                                }


                                h2 {
                                    text-align: center;
                                    margin: 21px 8px;
                                }
                            }
                        }
                    }
                }
            }

            //[END] LYT INT2

            //LYT INT3
            .int3 {
                display: grid;
                grid-template-columns: 1fr;
                grid-template-rows: auto 1fr;
                grid-auto-columns: 1fr;
                grid-auto-rows: 1fr;
                gap: 0px 0px;
                grid-auto-flow: row;
