            //[END] LYT SS1

            //LYT SS1.1
            .ss1_1 {
                display: grid;
                grid-template-columns: 1fr 1fr;
                grid-template-rows: 100%;
                grid-auto-columns: 1fr;
                grid-auto-rows: 1fr;
                gap: 0px 60px;
                grid-auto-flow: row;
                grid-template-areas:
                    "grd_left grd_right";

                .grd_left {
                    grid-area: grd_left;

