                        cursor: pointer;
                        float: right;
                        border-radius: 50%;
                        -moz-border-radius: 50%;
                        -webkit-border-radius: 50%;
                        box-shadow: 0.2rem 0.8rem 1.6rem var(--colorTrans2);
                        border: 4px solid #722022;

                        svg {}

                        @media screen and (max-width: 447px) {
                            clear: both;
                            float: none;
                        }
                    }
                }

                .grd_right {
                    grid-area: grd_right;

                    .cont {
                        width: 100%;
                        height: auto;
                        max-width: 600px;
                        padding: 16px;

                        h1 {
                            margin-left: 8px;
                            text-align: left;
                        }

                        .flds {
                            padding: 8px;
                            float: left;

                            &:nth-child(1n) {
                                width: 25%;
                            }

                            &:nth-child(2n) {
                                width: 75%;
                            }
                        }

                        .qsts {
                            padding: 8px;
                            clear: both;

                            h3 {
                                margin: 16px;
                                width: 100%;
                            }
                        }
                    }

                }
            }

