                            .grd_c21 {
                                display: flex;
                                justify-self: left;
                                align-items: Top;
                                padding: 8px;
                                grid-area: grd_c21;
                            }

                            .grd_c30 {
                                display: flex;
                                justify-self: left;
                                align-items: center;
                                grid-area: grd_c30;

                                svg {
                                    //background-color: $cc02;
                                    height: 8vw !important;
