            //LYT FP43
            .fp43 {
                display: grid;
                grid-template-columns: 1fr;
                grid-template-rows: auto 1fr;
                gap: 0px 0px;
                grid-template-areas:
                    "grd_title"
                    "grd_results";
                height: 100%;

                .grd_title {
                    padding: 1vh calc(1vw + var(--paddingXxlH1));
                    grid-area: grd_title;

                    H2 {
                        text-align: center;
                    }

                    H4 {
                        text-align: center;
                    }
                }

                .grd_results {
                    display: flex;
                    justify-self: center;
                    align-items: center;
                    grid-area: grd_results;

                    .rsrcs {
                        display: grid;
                        grid-template-columns: auto auto;
                        grid-template-rows: auto auto;
                        gap: 16px 16px;
                        grid-template-areas:
                            "grd_v1 grd_v2"
                            "grd_t1 grd_t2";

                        .grd_v1 {
                            grid-area: grd_v1;

                            div {
                                float: right;
                            }
                        }

                        .grd_t1 {
                            grid-area: grd_t1;
                        }

                        .grd_v2 {
                            grid-area: grd_v2;

                            div {
                                float: left;
                            }
                        }

                        .grd_t2 {
                            grid-area: grd_t2;
                        }
                    }
                }
            }

            //[END] LYT FP43

            //LYT FP44
            .fp44 {
                display: grid;
                grid-template-columns: 1fr;
                grid-template-rows: auto 1fr;
                grid-auto-columns: 1fr;
                grid-auto-rows: auto;
                gap: 0px 0px;
                grid-auto-flow: row;
                grid-template-areas:
                    "grd_line1"
                    "grd_line2";
                height: 100%;

                .grd_line1 {
                    padding-bottom: 12vh;
                    grid-area: grd_line1;

                    h1 {
                        padding: 0 1em;
                        text-align: center;
                    }
                }

