                    padding: 0 calc(.5rem + 23vw);
                    grid-area: grd_line2;

                    .mbl {
                        display: grid;
                        grid-template-columns: auto 1fr auto;
                        grid-template-rows: 1fr;
                        gap: 0px 0px;
                        grid-template-areas:
                            "grd_icon grd_desc grd_cta";

                        background-color: #fff;
                        margin: 0 32px 21px;
                        padding: 16px;
                        border-radius: 20px;
                        box-shadow: 0.2rem 0.8rem 1.6rem var(--colorTrans2);

                        .grd_icon {
                            display: flex;
                            align-items: center;
                            grid-area: grd_icon;

                            svg {
                                height: 56px !important;
                                width: 56px !important;
                                border-radius: 50%;
                                -moz-border-radius: 50%;
                                -webkit-border-radius: 50%;

                                .pathcc {
                                    fill: $cc07;
                                }
                            }
                        }

                        .grd_desc {
                            display: flex;
                            align-items: center;
                            padding-left: 21px;
                            grid-area: grd_desc;
                        }

                        .grd_cta {
                            display: flex;
                            align-items: center;
                            padding-left: 21px;
                            grid-area: grd_cta;

                            .input {
                                border: 2px solid #722022;
                                border-radius: 3rem;
                                margin-left: 8px;

                                .input_label {
                                    bottom: 60%;
                                    color: $cc07;
                                }

                                .input_num {
                                    display: block;
                                    margin: 0;
                                    padding: 7px;
                                    color: inherit;
                                    width: 100%;
                                    font-family: inherit;
                                    font-size: var(--inputFontSize);
                                    font-weight: inherit;
                                    line-height: var(--inputLineHeight);
                                    border: none;
                                    border-radius: 3rem;
                                    transition: box-shadow var(--transitionDuration);
                                    text-align: center;

                                    &::-webkit-outer-spin-button,
                                    &::-webkit-inner-spin-button {
                                        -webkit-appearance: none;
                                        margin: 0;
                                    }

                                    &[type=number] {
                                        -moz-appearance: textfield;
                                    }

                                    &::placeholder {
                                        color: #B0BEC5;
                                    }

                                    &:focus {
                                        outline: none;
                                        box-shadow: none;
                                    }
                                }
                            }

                            .custom_select {
                                margin: 0;
                            }
                        }
                    }
                }
            }

            //[END] LYT FP44

            //LYT FP45
            .fp45 {
                display: flex;
                justify-self: center;
                align-items: center;

                .cont {
                    display: grid;
                    grid-template-columns: auto auto;
                    grid-template-rows: auto auto;
                    gap: 0 0;
                    grid-template-areas:
                        "grd_title1 grd_title2"
                        "grd_r grd_date";
                    background-color: $ccw;
                    border-radius: 20px;
                    box-shadow: 0.2rem 0.8rem 1.6rem var(--colorTrans2);
                    margin: 16vh auto;

                    .grd_title1 {
                        padding: 16px 16px 0;
                        border-radius: 20px 0 0;
                        grid-area: grd_title1;

                        h2 {
                            margin-bottom: 0;
                        }
                    }

                    .grd_title2 {
                        padding: 16px 16px 0;
                        border-radius: 0 20px 0 0;
                        background-color: mix(#fff, $cc07, 84%);
                        grid-area: grd_title2;

                        h2 {
                            margin-bottom: 0;
                        }
                    }

                    .grd_r {

                        display: flex;
                        align-items: center;
                        grid-area: grd_r;

                        div {
                            margin: 0 auto;
                        }
                    }

                    .grd_date {
                        padding: 16px;
                        border-radius: 0 0 20px;
                        background-color: mix(#fff, $cc07, 84%);
                        grid-area: grd_date;
                    }
                }
            }

            //[END] LYT FP45
        }

        .grd_bottom_nav {
            grid-area: grd_bottom_nav;

            .btm_nab_one {
                display: grid;
                grid-template-columns: auto 1fr auto;
                grid-template-rows: 100%;
                grid-auto-columns: 1fr;
                grid-auto-rows: 1fr;
                gap: 0px 0px;
                grid-auto-flow: row;
                grid-template-areas:
                    "grd_btm_left grd_btm_center grd_btm_right";
                height: 100%;
                //display: none;



                .grd_btm_left {
                    display: flex;
                    justify-self: center;
                    align-items: center;
                    padding: 0 0 16px 30px;
                    grid-area: grd_btm_left;
                }

                .grd_btm_center {
                    display: flex;
                    justify-self: center;
                    align-items: center;
                    grid-area: grd_btm_center;

                    .audioplayer {
                        margin-top: -16px;
                    }
                }

                .grd_btm_right {
                    display: flex;
                    justify-self: center;
                    align-items: center;
                    padding: 0 30px 16px 0;
                    grid-area: grd_btm_right;
                }
            }
        }
    }

